import $ from "jquery";

class Cnt {
  constructor() {
    this.start();
    this.listener();
  }

  start() {
    const _this = this;
    window.chrome.runtime.sendMessage({
      action: "getOptions",
    });

    $(document).ready(function () {
      $(".ytp-right-controls").prepend(
        `<button class="source-scripts-common-_index_floatingPlayer_2R8 source-scripts-common-page-button-_Button_button_29H ytp-button" aria-pressed="false" lable="FP">
            <span class="">
              <span class="source-scripts-common-page-button-_Button_icon_3S4 source-scripts-common-page-button-_Button_floating_3Gk">
              </span>
              <div class="ytp-tooltip ytp-tooltip-text source-scripts-common-page-button-_Button_tooltip_3-L">Floating player</div></span>
          </button>`
      );

      $(".source-scripts-common-page-button-_Button_icon_3S4").css(
        "display",
        "block"
      );

      $(".source-scripts-common-_index_floatingPlayer_2R8").click(function () {
        _this.floatingPlayer();
      });
    });
  }

  listener() {
    const _this = this;
    window.chrome.runtime.onMessage.addListener((request, sender, callback) => {
      const { action, data } = request;

      if (action === "newConfig") {
        _this.applySettings(data);
      }
    });
  }

  applySettings(data) {
    const quality = data.quality.filter((i) => i.status === true);

    for (const key in data.main) {
      if (data.main[key] === false) {
        this.hideBtns(key);
      } else if (data.main[key] === true) {
        this.showBtns(key);
      }
    }

    this.applyQuality(quality);
  }

  applyQuality(quality) {
    const script = `(function () {
        let isEmbed = true;
        let playback;
        let quality = "${quality[0].value}";

        onYouTubePlayerReady = function (player) {
          document.addEventListener("updateQuality", function (e) {
            quality = e.quality;
            updQuality();
          });
          playback = false;
          globalPlayer = player;
          globalPlayer.addEventListener("onStateChange", function (state) {
            if (state == 3 && !playback) {
              updQuality();
            } else if (state == -1) {
              playback = false;
            } else if (state == 5) {
              isEmbed = true;
            }
          });

          globalPlayer.addEventListener(
            "onPlaybackQualityChange",
            function (event) {
              if (isEmbed) {
                if (globalPlayer.getAvailableQualityLevels === null) {
                  document.removeEventListener("updateQuality", function (e) {
                    quality = e.quality;
                    updQuality();
                  });
                  globalPlayer = document.getElementById("movie_player");
                }
                let avaliableQuality = globalPlayer.getAvailableQualityLevels();
                let pbQual =
                  avaliableQuality.indexOf(quality) == -1
                    ? avaliableQuality[0]
                    : quality;
                let qualIsCorrect = pbQual === globalPlayer.getPlaybackQuality();
                if (!qualIsCorrect) {
                  updQuality();
                }
                isEmbed = false;
              }
            }
          );
          updQuality();
        };

        updQuality = function () {
          var avaliableQuality = globalPlayer.getAvailableQualityLevels();
          var qual =
            avaliableQuality.length && avaliableQuality.indexOf(quality) == -1
              ? avaliableQuality[0]
              : quality;
          var playerState = globalPlayer.getPlayerState();
          playback = true;
          var now = new Date().getTime();
          let savedQuality = {
            creation: now,
            data: qual,
            expiration: now + 2592000000,
          };
          localStorage["yt-player-quality"] = JSON.stringify(savedQuality);
          globalPlayer.setPlaybackQuality(qual);
        };
      })();`;

    function injectCode(scriptText) {
      let script = document.createElement("script");
      script.textContent = scriptText;
      document.head.appendChild(script);
    }

    injectCode(script);
  }

  hideBtns(i) {
    $(document).ready(function () {
      if (i === "AE") {
        $(".source-scripts-common-_index_audioButton_3mS").css(
          "display",
          "none"
        );
      } else if (i === "CGS") {
        $(".source-scripts-common-_index_captureButton_PSG").css(
          "display",
          "none"
        );
      } else if (i === "CF") {
        $(".source-scripts-common-_index_videoButton_1Y6").css(
          "display",
          "none"
        );
      } else if (i === "FP") {
        $(".source-scripts-common-_index_floatingPlayer_2R8").css(
          "display",
          "none"
        );
      }
    });
  }

  showBtns(i) {
    $(document).ready(function () {
      if (i === "AE") {
        $(".source-scripts-common-_index_audioButton_3mS").css(
          "display",
          "inline-block"
        );
      } else if (i === "CGS") {
        $(".source-scripts-common-_index_captureButton_PSG").css(
          "display",
          "inline-block"
        );
      } else if (i === "CF") {
        $(".source-scripts-common-_index_videoButton_1Y6").css(
          "display",
          "inline-block"
        );
      } else if (i === "FP") {
        $(".source-scripts-common-_index_floatingPlayer_2R8").css(
          "display",
          "inline-block"
        );
      }
    });
  }

  async floatingPlayer() {
    const videos = Array.from(document.querySelectorAll("video"))
      .filter((video) => video.readyState !== 0)
      .filter((video) => video.disablePictureInPicture === false)
      .sort((v1, v2) => {
        const v1Rect = v1.getClientRects()[0];
        const v2Rect = v2.getClientRects()[0];
        return v2Rect.width * v2Rect.height - v1Rect.width * v1Rect.height;
      });

    if (videos.length === 0) return;

    const video = videos[0];

    if (video.hasAttribute("__pip__")) {
      await document.exitPictureInPicture();
    } else {
      await video.requestPictureInPicture();
      video.setAttribute("__pip__", true);
      video.addEventListener(
        "leavepictureinpicture",
        (event) => {
          video.removeAttribute("__pip__");
        },
        { once: true }
      );
    }
  }
}

const cnt = new Cnt();
